export class FuelWholesaleModel {
	id: number;
	amount: number;
	cumulativeSum: number;
	customerId: number;
	customer: string;
	categoryId: number;
	category: string;
	notes: string;
	sellingDate: string;

	clear() {
		this.id = 0;
		this.amount = 0;
		this.cumulativeSum = 0;
		this.customerId = 0;
		this.customer = '';
		this.categoryId = 0;
		this.category = '';
		this.notes = '';
		this.sellingDate = new Date().toISOString().substr(0, 16);
	}
}
