export class FuelPurchasesModel {
	id: number;
	category: string;
	categoryId: number;
	amountBought: string;
	categoryPrice: string;
	// currency: string;
	// currencyId: number;
	balance: string;
	supplierId: number;
	supplierName: string;
	cashTotal: string;
	transportationFees: string;
	purchaseDate: string;

	clear() {
		this.id = 0;
		this.category = '';
		this.amountBought = '';
		this.categoryPrice = '';
		this.balance = '';
		this.supplierName = '';
		this.cashTotal = '';
		this.transportationFees = '';
		this.purchaseDate = new Date().toISOString().substr(0, 16);
	}
}
