// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { FuelWholesaleModel } from '../models/fuel-wholesale.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'fuel-wholesale-edit-dialog',
	templateUrl: 'fuel-wholesale-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FuelWholesaleEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	fuelWholesale: FuelWholesaleModel;
	fuelWholesaleForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// Get from API
	fuelTypes = [];
	customers = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<FuelWholesaleEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.fuelWholesale = this.data.reading;
		this.fuelTypes = this.data.fuelTypes;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getFuelTypes();
		this.getWholeSaleCustomers();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let sellingDate = this.fuelWholesale.sellingDate;
		if (this.fuelWholesale.id > 0)
			sellingDate = new Date(+new Date(this.fuelWholesale.sellingDate) - this.tzoffset).toISOString().substr(0, 16);

		this.fuelWholesaleForm = this.fb.group({
			id: [this.fuelWholesale.id, Validators.required],
			category: [this.fuelWholesale.categoryId, Validators.required],
			customer: [this.fuelWholesale.customerId, Validators.required],
			amount: [this.fuelWholesale.amount, Validators.required],
			notes: [this.fuelWholesale.notes, ''],
			sellingDate: [sellingDate, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.fuelWholesale.id > 0) {
			return ` `;
		}

		return ' ';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.fuelWholesaleForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getFuelTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/Categories?Type=fuel',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.fuelTypes = res.data.map(item => {
				return {
					name: item.categoryName,
					id: item.id
				};
			});

			this.fuelWholesaleForm.controls["category"].setValue(this.fuelWholesale.categoryId);
		});
	}

	getWholeSaleCustomers() {
		this.http.get(
			globalVariables.baseUrl + 'api/WholeSale/WholeSaleCustomers',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.customers = res.data.map(item => {
				return {
					name: item.customerName,
					id: item.id
				}
			});

			this.fuelWholesaleForm.controls["customer"].setValue(this.fuelWholesale.customerId);
		});
	}

	/**
	 * Returns prepared fuel purchase
	 */
	prepareReading(): FuelWholesaleModel {
		const controls = this.fuelWholesaleForm.controls;
		const _fuelWholesale = new FuelWholesaleModel();
		_fuelWholesale.id = this.fuelWholesale.id;
		_fuelWholesale.categoryId = controls.category.value;
		_fuelWholesale.customerId = controls.customer.value;
		_fuelWholesale.amount = controls.amount.value;
		_fuelWholesale.notes = controls.notes.value;
		_fuelWholesale.sellingDate = controls.sellingDate.value;

		return _fuelWholesale;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.fuelWholesaleForm.controls;

		/** check form */
		if (this.fuelWholesaleForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel price
	 *
	 * @param _fuelWholesale: FuelWholesaleModel
	 */
	updateReading(_fuelWholesale: FuelWholesaleModel) {
		let sellingDate = new Date(+new Date(_fuelWholesale.sellingDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/Wholesale',
			{
				id: _fuelWholesale.id,
				categoryId: _fuelWholesale.categoryId,
				sellingDate,
				customerId: +_fuelWholesale.customerId,
				amount: +_fuelWholesale.amount,
				notes: _fuelWholesale.notes,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _fuelWholesale, isEdit: true });
			}, err => {
				let msg = '    ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Fuel price
	 *
	 * @param _fuelWholesale: FuelWholesaleModel
	 */
	createReading(_fuelWholesale: FuelWholesaleModel) {
		let sellingDate = new Date(+new Date(_fuelWholesale.sellingDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/Wholesale',
			{
				id: _fuelWholesale.id,
				categoryId: _fuelWholesale.categoryId,
				sellingDate,
				customerId: +_fuelWholesale.customerId,
				amount: +_fuelWholesale.amount,
				notes: _fuelWholesale.notes,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _fuelWholesale, isEdit: false });
			}, err => {
				let msg = '    ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
