// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { FuelPricesModel } from '../models/fuel-prices.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'fuel-prices-edit-dialog',
	templateUrl: 'fuel-prices-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FuelPricesEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	fuelPrice: FuelPricesModel;
	fuelPriceForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	fuelTypes = [];
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<FuelPricesEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.fuelPrice = this.data.reading;
		this.fuelTypes = this.data.fuelTypes;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getFuelTypes();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let date = this.fuelPrice.date;
		if (this.fuelPrice.id > 0)
			date = new Date(+new Date(this.fuelPrice.date)).toISOString().substr(0, 10);

		this.fuelPriceForm = this.fb.group({
			id: [this.fuelPrice.id, Validators.required],
			category: [this.fuelPrice.categoryId, Validators.compose([Validators.required, Validators.min(1)])],
			price: [this.fuelPrice.price, Validators.required],
			date: [date, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.fuelPrice.id > 0) {
			return `   '${this.fuelPrice.category}'`;
		}

		return ' ';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.fuelPriceForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getFuelTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/Categories?Type=fuel',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.fuelTypes = res.data.map(item => {
				return {
					name: item.categoryName,
					id: item.id
				}
			});

			this.fuelPriceForm.controls["category"].setValue(this.fuelPrice.categoryId);
		});
	}

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): FuelPricesModel {
		const controls = this.fuelPriceForm.controls;
		const _fuelPrice = new FuelPricesModel();
		_fuelPrice.id = this.fuelPrice.id;
		_fuelPrice.categoryId = controls.category.value;
		_fuelPrice.price = controls.price.value;
		_fuelPrice.date = controls.date.value;

		return _fuelPrice;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.fuelPriceForm.controls;
		console.log(this.fuelPriceForm);

		/** check form */
		if (this.fuelPriceForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel price
	 *
	 * @param _fuelPrice: FuelPricesModel
	 */
	updateReading(_fuelPrice: FuelPricesModel) {
		let date = new Date(+new Date(_fuelPrice.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/CategoryPrices',
			{
				..._fuelPrice,
				price: +_fuelPrice.price,
				creationDate: date
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _fuelPrice, isEdit: true });
			}, err => {
				let msg = '   ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Fuel price
	 *
	 * @param _fuelPrice: FuelPricesModel
	 */
	createReading(_fuelPrice: FuelPricesModel) {
		let date = new Date(+new Date(_fuelPrice.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/CategoryPrices',
			{
				..._fuelPrice,
				price: +_fuelPrice.price,
				creationDate: date
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _fuelPrice, isEdit: false });
			}, err => {
				let msg = '   ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
