// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { FuelPurchasesModel } from '../models/fuel-purchases.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'fuel-purchases-edit-dialog',
	templateUrl: 'fuel-purchases-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FuelPurchasesEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	fuelPurchase: FuelPurchasesModel;
	fuelPurchaseForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// Get from API
	fuelTypes = [];
	fuelSuppliers = [];
	currencies = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<FuelPurchasesEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.fuelPurchase = this.data.reading;
		this.fuelTypes = this.data.fuelTypes;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getFuelTypes();
		this.getFuelSuppliers();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let purchaseDate = this.fuelPurchase.purchaseDate;
		if (this.fuelPurchase.id > 0)
			purchaseDate = new Date(+new Date(this.fuelPurchase.purchaseDate) - this.tzoffset).toISOString().substr(0, 16);

		this.fuelPurchaseForm = this.fb.group({
			id: [this.fuelPurchase.id, Validators.required],
			category: [this.fuelPurchase.categoryId, Validators.required],
			supplierName: [this.fuelPurchase.supplierId, Validators.required],
			amountBought: [this.fuelPurchase.amountBought, Validators.required],
			// balance: [this.fuelPurchase.balance, Validators.required],
			// cashTotal: [this.fuelPurchase.cashTotal, Validators.required],
			// transportationFees: [this.fuelPurchase.transportationFees, Validators.required],
			purchaseDate: [purchaseDate, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.fuelPurchase.id > 0) {
			return `   '${this.fuelPurchase.category}'`;
		}

		return '  ';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.fuelPurchaseForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getFuelTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/Categories?Type=fuel',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.fuelTypes = res.data.map(item => {
				return {
					name: item.categoryName,
					id: item.id
				};
			});

			this.fuelPurchaseForm.controls["category"].setValue(this.fuelPurchase.categoryId);
		});
	}

	getFuelSuppliers() {
		this.http.get(
			globalVariables.baseUrl + 'api/Suppliers',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.fuelSuppliers = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});

			this.fuelPurchaseForm.controls["supplierName"].setValue(this.fuelPurchase.supplierId);
		});
	}

	getCurrencies() {
		this.http.get(
			globalVariables.baseUrl + 'api/Currencies',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.currencies = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});
		});
	}

	/**
	 * Returns prepared fuel purchase
	 */
	prepareReading(): FuelPurchasesModel {
		const controls = this.fuelPurchaseForm.controls;
		const _fuelPurchase = new FuelPurchasesModel();
		_fuelPurchase.id = this.fuelPurchase.id;
		_fuelPurchase.categoryId = controls.category.value;
		_fuelPurchase.supplierId = controls.supplierName.value;
		_fuelPurchase.amountBought = controls.amountBought.value;
		// _fuelPurchase.balance = controls.balance.value;
		// _fuelPurchase.cashTotal = controls.cashTotal.value;
		// _fuelPurchase.transportationFees = controls.transportationFees.value;
		_fuelPurchase.purchaseDate = controls.purchaseDate.value;

		return _fuelPurchase;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.fuelPurchaseForm.controls;
		console.log(this.fuelPurchaseForm);

		/** check form */
		if (this.fuelPurchaseForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel price
	 *
	 * @param _fuelPurchase: FuelPurchasesModel
	 */
	updateReading(_fuelPurchase: FuelPurchasesModel) {
		let purchaseDate = new Date(+new Date(_fuelPurchase.purchaseDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/FuelPurchases',
			{
				id: _fuelPurchase.id,
				categoryId: _fuelPurchase.categoryId,
				fuelPurchasedAt: purchaseDate,
				supplierId: +_fuelPurchase.supplierId,
				amountBought: +_fuelPurchase.amountBought,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _fuelPurchase, isEdit: true });
			}, err => {
				let msg = '    ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Fuel price
	 *
	 * @param _fuelPurchase: FuelPurchasesModel
	 */
	createReading(_fuelPurchase: FuelPurchasesModel) {
		let purchaseDate = new Date(+new Date(_fuelPurchase.purchaseDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/FuelPurchases',
			{
				categoryId: _fuelPurchase.categoryId,
				fuelPurchasedAt: purchaseDate,
				supplierId: +_fuelPurchase.supplierId,
				amountBought: +_fuelPurchase.amountBought,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _fuelPurchase, isEdit: false });
			}, err => {
				let msg = '    ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
