// Angular
import {Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
// Material
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
// RxJS
import {Subscription, of} from 'rxjs';
import {delay} from 'rxjs/operators';
// NGRX
import {Update} from '@ngrx/entity';
import {Store, select} from '@ngrx/store';
// State
import {AppState} from '../../../../../../core/reducers';
// CRUD
import {LayoutUtilsService, TypesUtilsService} from '../../../../../../core/_base/crud';
// Services and Models
import {selectCustomersActionLoading} from '../../../../../../core/e-commerce';
import {FuelPaymentModel} from '../models/fuel-payment.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import globalVariables from '../../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'fuel-purchases-payments-details-edit-dialog',
	templateUrl: 'fuel-purchases-payments-details-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FuelPurchasesPaymentsDetailsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	fuelPurchase: FuelPaymentModel;
	fuelPurchaseForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	// TODO: Get from Api
	paymentTypes = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<FuelPurchasesPaymentsDetailsEditDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private fb: FormBuilder,
				private store: Store<AppState>,
				private layoutUtilsService: LayoutUtilsService,
				private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.fuelPurchase = this.data.reading;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		this.fuelPurchaseForm = this.fb.group({
			id: [this.fuelPurchase.id, Validators.required],
			paymentType: [this.fuelPurchase.paymentType, Validators.required],
			paymentValue: [this.fuelPurchase.paymentValue, Validators.required]
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.fuelPurchase.id > 0) {
			return ` `;
		}

		return '  ';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.fuelPurchaseForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getPaymentTypes() {
		this.http.get(
			globalVariables.baseUrl + '/api/PaymentsMethods',
			{headers: this.headers}
		).subscribe((res: any) => {
			this.paymentTypes = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});
		});
	}

	/**
	 * Returns prepared fuel purchase
	 */
	prepareReading(): FuelPaymentModel {
		const controls = this.fuelPurchaseForm.controls;
		const _fuelPurchase = new FuelPaymentModel();
		_fuelPurchase.id = this.fuelPurchase.id;
		_fuelPurchase.paymentValue = controls.paymentValue.value;
		_fuelPurchase.paymentType = controls.paymentType.value;

		return _fuelPurchase;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.fuelPurchaseForm.controls;
		console.log(this.fuelPurchaseForm);

		/** check form */
		if (this.fuelPurchaseForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Fuel Payment
	 *
	 * @param _fuelPurchase: FuelPaymentModel
	 */
	updateReading(_fuelPurchase: FuelPaymentModel) {
		this.http.put(
			globalVariables.baseUrl + 'api/FuelPurchases/Payments',
			{
				fuelPurchasesId: _fuelPurchase.id,
				paymentType: _fuelPurchase.paymentType,
				paidMoney: +_fuelPurchase.paymentValue
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_fuelPurchase, isEdit: true});
			}, err => {
				let msg = '   ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Fuel Payment
	 *
	 * @param _fuelPurchase: FuelPaymentModel
	 */
	createReading(_fuelPurchase: FuelPaymentModel) {
		this.http.post(
			globalVariables.baseUrl + 'api/FuelPurchases/Payments',
			{
				fuelPurchasesId: _fuelPurchase.id,
				paymentType: _fuelPurchase.paymentType,
				paidMoney: +_fuelPurchase.paymentValue
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_fuelPurchase, isEdit: false});
			}, err => {
				let msg = '   ';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
